.store-locator-main{
  .slick-prev:before {
    color: var(--yellow) !important;
    background-color: #eee;
    border-radius: 50%;
  }
  .slick-next:before {
    color: var(--yellow) !important;
    background-color: #eee;
    border-radius: 50%;
  }
  .slick-dots {
    bottom: 15px;

    li {
      width: 30px;

      button {
        background-color: var(--secondary);
        height: 8px;
        width: 8px;
        padding: 3px;
        border-radius: 50%;
        display: inline-block;

        &:before {
          content: none;
        }
      }
    }

    .slick-active {
      button {
        background-color: var(--yellow);
        height: 0;
        width: 30px;
        border-radius: 15px;
      }
    }
  }
  .store-slider{
    div{
      border-radius: 15px;
      img{
        border-radius: 15px;
      }
    }
  }
}